import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import App from './App';

import { BrowserRouter } from 'react-router-dom';

ReactDOM.render(
  <BrowserRouter basename="/need_for_drive">
    <App />
  </BrowserRouter>,
  document.getElementById('root')
);

